.sidedrawer {
    width: 40%;
    height: 100vh;
    background-color: silver;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 200;
    transform: translateX(-100%);
    transition: all 0.3s ease-out;
    display: flex;
    flex-direction: column;
    justify-content: left;
}

.sidedrawer.show {
    transform: translateX(0);
}

.sidedrawer-links {
    display: flex;
    flex-direction: column;
    list-style: none;
}

.sidedrawer-links > li {
    display: flex;
    align-items: center;
}

.sidedrawer-links > li > a {
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    flex: 1;
    padding: 1rem;
    text-decoration: none;
    color: #171717;
    font-size: 1.6rem;
}

.sidedrawer-links > li > a:hover {
    background: rgb(185, 99, 247);
    color: turquoise;
}

.sidedrawer-links > li > a > span {
    display: flex;
    align-items: center;
    margin-left: 8px;
}

.sidedrawer-badge {
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    width: 30px;
    height: 30px;
    border-radius: 50%;
    background-color: #171717;
    color:antiquewhite;
    margin-left: 8px;
}

@media(min-width: 960px) {
    .sidedrawer {
        display: none;
    }
}