.cartscreen {
    display: flex;
    max-width: 1300px;
    margin-top: 2rem auto;
}

.cartscreen h2 {
    margin-bottom: 1rem;
}

.cartscreen-left {
    flex: 0.7;
    margin-right: 1rem;
    background-color: transparent;
    padding: 1rem;
}

.cartscreen-right {
    flex: 0.3;
    background: #f4f4f4;
    box-shadow: 0 1px 4px rgba(0, 0, 0, 0.4);
    height: fit-content;
}

.cartscreen-right div {
    border-bottom: 1px solid rgba(0, 0, 0, 0.2);
    padding: 1rem;
}

.cartscreen-right div:last-child {
    border: none;
}

.cartscreen-info p {
    padding: 8px;
}

.checkout-btn, .return-btn{
    padding: 10px 17px;
    width: 100%;
    background: #171717;
    color: #f4f4f4;
    border: 1px solid #171717;
    cursor: pointer;
    text-decoration: none;
}

.checkout-btn:hover, .return-btn:hover {
    opacity: 0.9;
    color: #f4f4f4;
}

@media (max-width: 1320px) {
    .cartscreen {
        max-width: 900px;
    }
}

@media (max-width: 960px) {
    .cartscreen {
        max-width: 800px;
        flex-direction: column;
    }
    .cartscreen-right {
        margin: 1rem;
    }
    .cartscreen-left {
        margin: 0;
    }
}